@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 20px;
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  width: 100%;
}

#root {
  display: grid;
  grid-template-columns: 1fr;
  place-items: center;
  background-color: rgb(75, 74, 74);
  width: 100%;
  height: 100vh;
  overflow-y: hidden;
}

.container {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 2fr;
  place-items: flex-start;
  width: 30%;
  height: 95vh;
  margin: 1%;
  padding: 1%;
  background-color: #dde0e0;
  border-radius: 5px;
  overflow-y: auto;
}

@media screen and (max-width:600px) {
  .container {
    padding: 0 2%;
    width: 100%;
    height: 100vh;
  }
}
.ExpenseItem {
  width: 100%;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 2fr 1fr;
      grid-template-columns: 2fr 1fr;
  -ms-grid-rows: minmax(80px, auto);
      grid-template-rows: minmax(80px, auto);
  place-items: center;
  padding: 0 2%;
  margin: 1%;
  border-radius: 5px;
  background-color: #fff;
}

.ExpenseItem .expense_item__info {
  padding: 1%;
  width: 100%;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr;
      grid-template-columns: 1fr;
}

.ExpenseItem .expense_item__info .expense_item__date {
  width: 100%;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr;
      grid-template-columns: 1fr;
  padding: 0 2%;
  color: #585252;
  font-weight: lighter;
  font-size: 0.9rem;
}

.ExpenseItem .expense_item__info .expense_item__title {
  color: #022e57;
  width: 100%;
  padding: 1%;
  font-weight: 500;
  font-size: 1.1rem;
  text-transform: capitalize;
}

.ExpenseItem .expense_item__amount {
  background-color: #ff96ad;
  border-radius: 5px;
  -webkit-align-self: flex-start;
          align-self: flex-start;
  justify-self: flex-end;
  color: #022e57;
  font-weight: bold;
  padding: 2% 4%;
  margin-top: 0.6rem;
}

@media screen and (max-width: 600px) {
  .ExpenseItem {
    margin: 2% 0;
  }
  .ExpenseItem .expense_item__info .expense_item__date {
    font-size: 0.9rem;
  }
  .ExpenseItem .expense_item__info .expense_item__title {
    font-size: 1.1rem;
  }
}
